.gushi_header{
    margin: 0.3rem 0 1.2rem;
    width: 100%;
    .am-search{
        background-color: #fff;
        box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.01), 0px 2px 5px rgba(0, 0, 0, 0.12);
        margin-bottom: 0.2rem;
        .am-search-synthetic-ph{
            text-align: left;
            padding-left: 5px;
        }
    }
    .am-tabs-default-bar-tab-active{
        color: #008489;
    }
}
.storybox{
    margin-top: 0.2rem;
    width: 100%;
    display: flex;
    flex-wrap:wrap;
    justify-content: space-between;
    .onestory{
        width: 48%;
        margin-bottom: 15px;
        img{
            width: 100%;
            height: 3.3rem;
            border-radius: 7px;
        }
        .story_title{
            font-size: 0.28rem;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            margin: 0.1rem 0;
            span{
                font-weight: bold;
                font-size: 0.26rem;
                margin-right: 0.1rem;
                line-height: 0.4rem;
            }
        }
        .story_info{
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size: 0.24rem;
            span{
                display: flex;
                align-items: center;
                img{
                    width: 0.45rem;
                    height: 0.45rem;
                    border-radius:50%;
                    margin-right: 0.1rem;
                }
                i{
                    font-size: 0.4rem;
                    font-weight: bold;
                }
                b{
                    font-size: 0.26rem;
                    font-weight: normal;
                }
            }
        }
    }
}